import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import { Divider } from '@material-ui/core';
import Badge from '@material-ui/core/Badge';
import { withStyles } from '@material-ui/core/styles';
import Medal from './Medal';

const styles = {
  country: {
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: 10,
    padding: 18,
    width: 260,
  },
  divider: {
    marginTop: 5,
    marginBottom: 10,
  },
  countryName: {
    fontSize: '1.8em',
    paddingRight: '4px',
  },
}

class Country extends Component {
  getMedalsTotal(country, medals) {
    let sum = 0;
    medals.forEach(medal => {
      sum += country[medal.name + 'MedalCount'];
    });
    return sum;
  }
  render() { 
    const { classes, country, medals, onIncrement, onDecrement } = this.props;
    return (
      <Paper elevation={4} className={classes.country}>
        <Badge badgeContent={ this.getMedalsTotal(country, medals) } color='primary'>
          <div className={classes.countryName}>
            { country.name }
          </div>
        </Badge>
        <Divider className={classes.divider} />
        { medals.map(medal =>
          <Medal 
            key={ medal.id } 
            country={ country } 
            medal={ medal } 
            onIncrement={ onIncrement } 
            onDecrement={ onDecrement } />
        ) }
      </Paper>
    );
  }
}
 
export default withStyles(styles, { withTheme: true })(Country);
